                </div>
            </div>
          </div>

        </div>
    </nav>
</template>

<script>
export default {
    data: () => ({
        isOpen: false
    }),
    methods: {
        toggleNav () {
            this.isOpen = !this.isOpen;
        },
        scrollTo (sectionName) {
            if(this.isOpen == true) {
                this.toggleNav();
            }
            this.$emit("scroll-to", sectionName, this.isOpen);
        }
    }
}
</script>

