<template>
    <div class="modal" :class="{'is-active': isOpen}">
        <div class="modal-background" @click="closeModal"></div>
        <div class="modal-content" @keydown.esc="closeModal">
            <div class="container box has-text-centered" role="dialog" aria-labelledby="mainModalTitle">
                <p class="title" id="mainModalTitle" ref="modalTitle" tabindex="0" @keydown.shift.tab.prevent="">{{ cardInfo.title }}</p>
                <figure class="image is-16by9">
                    <iframe class="has-ratio" width="640" height="360" :src="cardInfo.video" frameborder="0" :title="cardInfo.ariaDemo" allowfullscreen></iframe>
                </figure>
                <hr>
                <div class="buttons">
                    <a :href="cardInfo.source" :aria-label="cardInfo.ariaSource" ref="sourceBtn" class="button is-fullwidth" target="_blank" rel="noreferrer">View Source</a>
                    <a class="button is-fullwidth" disabled aria-disabled="true" v-if="cardInfo.site === 'NA'">{{ cardInfo.note }}</a>
                    <a :href="cardInfo.site" :aria-label="cardInfo.ariaSite" class="button is-fullwidth" target="_blank" rel="noreferrer" v-else>View Site</a>
                </div>
            </div>
        </div>
        <button class="modal-close is-large" aria-label="close" @click="closeModal" @keydown.tab.exact.prevent=""></button>
    </div>
</template>

<script>
import mainProjects from '@/models/mainProjects'

export default {
    props: {
        isOpen: Boolean,
        i: Number
    },
    data: () => ({
        mainProjects
    }),
    computed: {
        cardInfo: function () {
            return mainProjects.mainProjects[this.i]
        }
    },
    methods: {
        closeModal () {
            this.$emit("close-modal");
        },
        
    },
    watch: {
        //Set the focus to the opened modal
        isOpen: function () {
            if(this.isOpen == true) {
                this.$nextTick(() => {
                    const newFocus = this.$refs.modalTitle;
                    newFocus.focus();
                })
            }
        }
    }
    
}
</script>

<style scoped>
    #mainModalTitle:focus {
        outline: none;
    }
    .modal-close:hover {
        background: hsl(0, 0%, 29%);
    }
    .modal-close:focus {
        background: hsl(0, 0%, 29%);
    }
</style>